<template>
  <div>
    <!-- 面包屑导航 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>商品列表</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片内容区域 -->
    <el-card>
      <el-row>
        <!-- 文本搜索框 -->
        <el-col :span="8">
          <el-input @change="getGoodsList()" @clear="getGoodsList()" clearable v-model="queryInfo.query" placeholder="请输入内容" class="input-with-select">
            <el-button @click="getGoodsList()" slot="append" icon="el-icon-search"></el-button>
          </el-input>
        </el-col>
        <!-- 添加按钮 -->
        <el-col :span="7" class="addGoodsCol">
          <el-button @click="addGoods" type="primary">添加商品</el-button>
        </el-col>
      </el-row>
      <!-- 商品列表 -->
      <el-table stripe border :data="goodsListData.goods" style="width: 100%">
        <el-table-column type="index" label="#"> </el-table-column>
        <el-table-column prop="goods_name" label="商品名称"> </el-table-column>
        <el-table-column prop="goods_price" label="商品价格（元）" width="95"> </el-table-column>
        <el-table-column prop="goods_number" label="商品重量" width="80"> </el-table-column>
        <el-table-column prop="add_time" label="创建时间" width="160">
          <template v-slot="scope">
            {{ scope.row.add_time | dataFormat }}
          </template>
        </el-table-column>
        <el-table-column label="操作" width="130">
          <template v-slot="scope">
            <el-button @click="EditGoods(scope.row.goods_id)" size="mini" type="primary" icon="el-icon-edit"></el-button>
            <el-button @click="removeGoodsById(scope.row.goods_id)" size="mini" type="danger" icon="el-icon-delete"></el-button>
          </template>
        </el-table-column>
      </el-table>

      <!-- 分页 -->
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="queryInfo.pagenum" :page-sizes="[5, 10, 15, 20]" :page-size="queryInfo.pagesize" layout="total, sizes, prev, pager, next, jumper" :total="total"> </el-pagination>
    </el-card>
  </div>
</template>

<script>
export default {
  name: 'Goods',
  data() {
    return {
      // 商品列表请求数据
      queryInfo: {
        // 查询参数
        query: '',
        // 默认页码
        pagenum: 1,
        // 默认分页显示行数
        pagesize: 10
      },
      // 商品列表数据
      goodsListData: [],
      // 分页tatal
      total: 0
    }
  },
  created() {
    this.getGoodsList()
  },
  methods: {
    // 获取商品列表
    async getGoodsList() {
      const { data: res } = await this.$http.get('/goods', { params: this.queryInfo })
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg)
      }
      // this.$message.success('获取商品列表成功！')
      this.goodsListData = res.data
      console.log(res)
      this.total = res.data.total
    },
    // 分页监听事件
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getGoodsList()
    },
    handleCurrentChange(newCum) {
      this.queryInfo.pagenum = newCum
      this.getGoodsList()
    },
    // 根据商品id删除商品
    async removeGoodsById(id) {
      const comfirmResult = await this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).catch(err => err)
      if (comfirmResult !== 'confirm') {
        return this.$message.info('取消删除成功！')
      }

      const { data: res } = await this.$http.delete(`/goods/${id}`)
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg)
      }
      this.$message.success(res.meta.msg)
      this.getGoodsList()
    },
    addGoods() {
      this.$router.push('/add')
    },
    EditGoods(id) {
      this.$router.push(`/edit/${id}`)
    }
  }
}
</script>

<style lang="less" scoped>
.addGoodsCol {
  margin-left: 30px;
}
</style>
