<template>
  <div>
    <!-- 面包屑导航 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>修改商品</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片视图 -->
    <el-card>
      <!-- 警告提示区域 -->
      <el-alert title="修改商品信息" type="info" description="按照下面步骤添加商品信息" show-icon> </el-alert>
      <!-- 步骤条 -->
      <el-steps align-center :space="200" :active="activeIndex - 0" finish-status="success">
        <el-step title="基本信息"></el-step>
        <el-step title="商品参数"></el-step>
        <el-step title="商品属性"></el-step>
        <el-step title="商品图片"></el-step>
        <el-step title="商品内容"></el-step>
        <el-step title="完成"></el-step>
      </el-steps>

      <!-- teb栏页 -->
      <el-form :model="editGoodsForm" :rules="editGoodsFormRules" ref="editGoodsFormRef" label-width="70px" label-position="top">
        <el-tabs @tab-click="tabClick" :before-leave="beforeLeave" v-model="activeIndex" :tab-position="'left'">
          <el-tab-pane label="商品信息" name="0">
            <el-form-item label="商品名称" prop="goods_name">
              <el-input v-model="editGoodsForm.goods_name"></el-input>
            </el-form-item>
            <el-form-item label="商品价格" prop="goods_price">
              <el-input type="number" v-model="editGoodsForm.goods_price"></el-input>
            </el-form-item>
            <el-form-item label="商品重量" prop="goods_weight">
              <el-input type="number" v-model="editGoodsForm.goods_weight"></el-input>
            </el-form-item>
            <el-form-item label="商品数量" prop="goods_number">
              <el-input type="number" v-model="editGoodsForm.goods_number"></el-input>
            </el-form-item>
            <el-form-item label="商品分类" prop="goods_cat">
              <el-cascader style="width: 300px" v-model="editGoodsForm.goods_cat" :options="cateList" :props="cascaderProps"></el-cascader>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="商品参数" name="1">
            <el-form-item :label="item.attr_name" v-for="item in cateTabList" :key="item.attr_id">
              <!-- 复选框 -->
              <el-checkbox-group v-model="item.attr_vals">
                <el-checkbox border :label="cb" v-for="(cb, i) in item.attr_vals" :key="i"></el-checkbox>
              </el-checkbox-group>
            </el-form-item>
          </el-tab-pane>

          <el-tab-pane label="商品属性" name="2">
            <el-form-item v-for="item in onlyCateData" :key="item.attr_id" :label="item.attr_name">
              <el-input v-model="item.attr_vals"></el-input>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="商品图片" name="3">
            <!-- 图片上传 -->
            <el-upload :file-list="imgList" :action="actionURL" :on-preview="handlePreview" :on-remove="handleRemove" list-type="picture" :headers="headersToken" :on-success="uploadImgSuccess">
              <el-button size="small" type="primary">点击上传</el-button>
            </el-upload>
          </el-tab-pane>

          <el-tab-pane label="商品内容" name="4">
            <!-- 富文本编辑器 -->
            <quill-editor v-model="editGoodsForm.goods_introduce"></quill-editor>
            <el-button class="addBtn" type="primary" @click="addGoodsData">添加商品</el-button>
          </el-tab-pane>
        </el-tabs>
      </el-form>
    </el-card>
    <!-- 图片预览对话框 -->
    <el-dialog title="图片预览" :visible.sync="imgDialogVisible" width="50%">
      <img class="addImg" :src="handleImg" />
    </el-dialog>
  </div>
</template>
<script>
// 导入lodash组件
import _ from 'lodash'

export default {
  name: 'Add',
  data() {
    return {
      // 步骤条索引，控制台选中step
      activeIndex: '0',
      // 修改商品form表单数据
      editGoodsForm: {
        goods_name: '',
        goods_price: 0,
        goods_weight: 0,
        goods_number: 0,
        // 商品所属分类数组
        goods_cat: [],
        // 上传图片数组
        pics: [],
        // 富文本编辑器描述
        goods_introduce: '',
        // 动态属性和静态参数
        attrs: []
      },
      // 添加商品form表单验证规则
      editGoodsFormRules: {
        goods_name: [{ required: true, message: '请输入商品名称', trigger: 'blur' }],
        goods_price: [{ required: true, message: '请输入商品价格', trigger: 'blur' }],
        goods_weight: [{ required: true, message: '请输入商品价格', trigger: 'blur' }],
        goods_number: [{ required: true, message: '请输入商品价格', trigger: 'blur' }],
        goods_cat: [{ required: true, message: '请输入商品分类', trigger: 'blur' }]
      },
      // 分类列表数据
      cateList: [],
      // 级联选择器prop数据
      cascaderProps: {
        expandTrigger: 'hover',
        label: 'cat_name',
        value: 'cat_id',
        children: 'children'
      },
      // tab标签列表数据
      // 动态参数
      cateTabList: [],
      // 静态属性
      onlyCateData: [],
      actionURL: 'http://8.131.62.137:1016/api/private/v1/upload',
      // 指定上传图片的headers请求头
      headersToken: {
        Authorization: window.sessionStorage.getItem('token')
      },
      // 已上传图片列表
      imgList: [],
      // 图片预览url
      handleImg: '',

      imgDialogVisible: false
    }
  },
  created() {
    this.getCateList()
    this.getGoodsById()
  },
  methods: {
    // 根据id获取商品数据
    async getGoodsById() {
      // console.log(this.$route.params.id)
      const { data: res } = await this.$http.get(`/goods/${this.$route.params.id}`)
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg)
      }
      this.editGoodsForm = res.data
      // 将级联选择器选择分类从字符串转换成数组，因为级联选择器只能接受数组
      this.editGoodsForm.goods_cat = this.editGoodsForm.goods_cat.split(',').map(Number)
      this.cateList = this.editGoodsForm.goods_cat
      console.log(this.editGoodsForm)
      this.getImgList()
    },
    // 获取修改商品分类列表
    async getCateList() {
      const { data: res } = await this.$http.get('/categories')
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg)
      }
      console.log(res)
      this.cateList = res.data
    },
    // 处理商品参数数据
    // getAttrDtat() {
    //   this.editGoodsForm.attrs.forEach(item => {
    //     if (item.attr_sel === 'many') {
    //       // 全部参数
    //       item.attr_vals = item.attr_vals ? item.attr_vals.split(',') : []
    //       // 已选参数
    //       item.attr_value = item.attr_value ? item.attr_value.split(',') : []
    //       this.cateTabList.push(item)
    //     } else if (item.attr_sel === 'only') {
    //       this.onlyCateData.push(item)
    //     }
    //   })
    // },
    // 图片文件
    getImgList() {
      this.editGoodsForm.pics.forEach(item => {
        this.imgList.push({
          name: item.pics_id,
          url: item.pics_big_url,
          pic: item.pics_big
        })
        // console.log(this.imgList)
      })
    },
    // handleChange() {
    //   console.log(this.addGoodsForm.goods_cat)
    //   // 判断联级选择器选中为三级，否则清空数组
    //   if (this.addGoodsForm.goods_cat.length !== 3) {
    //     this.addGoodsForm.goods_cat = []
    //   }
    // },
    // 阻止tab标签也切换
    beforeLeave(activeName, oldActiveName) {
      // oldActiveName值等于0时，表示要离开当前的页面，判断商品分类是否选中
      // console.log('切换activeName' + activeName)
      // console.log('切换oldActiveName' + oldActiveName)
      if (oldActiveName === '0' && this.editGoodsForm.goods_cat.length !== 3) {
        this.$message.error('请先选择商品分类')
        return false
      }
    },
    // 获取分类静态属性
    async tabClick() {
      // console.log(this.activeIndex)
      // 点击tab栏第二级时
      if (this.activeIndex === '1') {
        const { data: res } = await this.$http.get(`/categories/${this.cateId}/attributes`, { params: { sel: 'many' } })
        if (res.meta.status !== 200) {
          return this.$message.error('获取商品分类属性失败！')
        }
        console.log(res)

        // item.attr_vals项从字符串以逗号的方式分隔为数组
        res.data.forEach(item => {
          item.attr_vals = item.attr_vals.length === 0 ? [] : item.attr_vals.split(',')
        })
        this.cateTabList = res.data
        console.log(this.cateTabList)
      } else if (this.activeIndex === '2') {
        const { data: res } = await this.$http.get(`/categories/${this.cateId}/attributes`, { params: { sel: 'only' } })
        if (res.meta.status !== 200) {
          return this.$message.error('获取商品分类属性失败！')
        }
        console.log(res.data)
        this.onlyCateData = res.data
      }
    },
    // 处理图片预览效果
    handlePreview(file) {
      console.log(file)
      this.handleImg = file.url
      this.imgDialogVisible = true
    },
    // 处理移除图片操作
    handleRemove(files) {
      console.log(files)
      if (files.response) {
        // 删除图片的临时路径
        const filesPath = files.response.data.tmp_path
        // 根据图片临时路径查找数组的索引
        const i = this.editGoodsForm.pics.findIndex(x => x.pic === filesPath)
        // 根据索引删除数组中对应的图片临时路径
        this.editGoodsForm.pics.splice(i, 1)
      } else {
        // 删除图片的临时路径
        const filesPath = files.pic
        // 根据图片临时路径查找数组的索引
        const i = this.editGoodsForm.pics.findIndex(x => x.pic === filesPath)
        // 根据索引删除数组中对应的图片临时路径
        this.editGoodsForm.pics.splice(i, 1)
      }
      //
      // this.editGoodsForm.uploadImg = this.imgList
    },
    // 图片上传成功后的操作
    uploadImgSuccess(response) {
      console.log(response)
      if (response.meta.status === 200) {
        // 1.处理上传信息
        const pics = { pic: response.data.tmp_path }
        // 2.最加到图片上传数组
        console.log(this)
        this.editGoodsForm.pics.push(pics)
        this.$message.success('上传图片成功！')
        console.log(this.editGoodsForm.pics)
      } else {
        this.$message.error('上传图片失败')
      }
    },
    // 添加商品
    addGoodsData() {
      this.$refs.editGoodsFormRef.validate(async valid => {
        if (!valid) return this.$message.error('请填写表单必填项！')

        // this.addGoodsForm.goods_cat = this.addGoodsForm.goods_cat.join(',')
        // console.log(this.addGoodsForm)

        // 使用lodash实现深拷贝
        const form = _.cloneDeep(this.editGoodsForm)
        form.goods_cat = form.goods_cat.join(',')

        // 处理动态参数
        this.cateTabList.forEach(item => {
          const newInfo = {
            attr_id: item.attr_id,
            attr_value: item.attr_vals.join(',')
          }
          // 将处理好的动态参数push追加到attrs数组
          this.editGoodsForm.attrs.push(newInfo)
        })
        // 处理静态属性
        this.onlyCateData.forEach(item => {
          const newInfo = {
            attr_id: item.attr_id,
            attr_value: item.attr_vals
          }
          this.editGoodsForm.attrs.push(newInfo)
        })
        form.attrs = this.editGoodsForm.attrs
        console.log(form)
        console.log(form.goods_id)

        // 发起商品添加axios
        const { data: res } = await this.$http.put(`/goods/${form.goods_id}`, form)
        if (res.meta.status !== 200) {
          return this.$messags.error(res.meta.msg)
        }
        this.$message.success('修改商品成功！')
        this.$router.push('/goods')
      })
    }
  },
  computed: {
    cateId() {
      if (this.editGoodsForm.goods_cat.length === 3) {
        console.log(this.editGoodsForm.goods_cat[2])
        return this.editGoodsForm.goods_cat[2]
      }
      return null
    }
  }
}
</script>

<style lang="less" scoped>
.el-alert {
  margin-bottom: 20px;
}
.el-checkbox {
  margin: 0 10px 0 0 !important;
}
.addImg {
  width: 100%;
}
.addBtn {
  margin-top: 15px;
}
</style>
