<template>
  <div>
    <!-- 面包屑导航 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>商品参数</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片内容区域 -->
    <el-card>
      <!-- 告警提示 -->
      <el-alert show-icon :closable="false" title="注意：只允许为第三级分类设置相关参数！" type="warning"> </el-alert>
      <!--  -->
      <el-row class="goods-par">
        <el-col>
          <span>选择商品分类：</span>
          <el-cascader clearable v-model="selectedCateKeys" :options="cateListInfo" :props="cateCascaderProps" @change="cateHandleChange"></el-cascader>
        </el-col>
      </el-row>

      <!-- tab标签页 -->
      <el-tabs v-model="cateActiveName" @tab-click="cateHandleClick">
        <el-tab-pane label="动态参数" name="many">
          <el-button @click="addDialogVisible = true" :disabled="isBtnDisabled" type="primary">添加参数</el-button>

          <!--动态参数表格 -->
          <el-table stripe border :data="manyTableData" style="width: 100%">
            <el-table-column type="expand">
              <template v-slot="scope">
                <!--循环渲染tag标签 -->
                <el-tag @close="removeParamsTag(i, scope.row)" closable class="tagParams" type="success" v-for="(item, i) in scope.row.attr_vals" :key="i">{{ item }}</el-tag>
                <!-- 输入文本框 -->
                <el-input class="input-new-tag" v-if="scope.row.inputVisible" v-model="scope.row.inputValue" ref="saveTagInput" size="small" @keyup.enter.native="handleInputConfirm(scope.row)" @blur="handleInputConfirm(scope.row)"> </el-input>
                <!-- 添加tag按钮 -->
                <el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ New Tag</el-button>
              </template>
            </el-table-column>
            <el-table-column label="#" type="index"></el-table-column>
            <el-table-column prop="attr_name" label="参数名称"> </el-table-column>
            <el-table-column label="操作">
              <template v-slot="scope">
                <el-button @click="editBtnDialog(scope.row)" icon="el-icon-edit" type="primary" size="mini">编辑</el-button>
                <el-button @click="removeParams(scope.row.attr_id)" icon="el-icon-delete" type="danger" size="mini">删除</el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>

        <!-- 静态属性 -->
        <el-tab-pane label="静态属性" name="only">
          <el-button @click="addDialogVisible = true" :disabled="isBtnDisabled" type="primary">添加属性</el-button>
          <!--动态参数表格 -->
          <el-table stripe border :data="onlyTableData" style="width: 100%">
            <el-table-column type="expand">
              <template v-slot="scope">
                <!--循环渲染tag标签 -->
                <el-tag @close="removeParamsTag(i, scope.row)" closable class="tagParams" type="success" v-for="(item, i) in scope.row.attr_vals" :key="i">{{ item }}</el-tag>
                <!-- 输入文本框 -->
                <el-input class="input-new-tag" v-if="scope.row.inputVisible" v-model="scope.row.inputValue" ref="saveTagInput" size="small" @keyup.enter.native="handleInputConfirm(scope.row)" @blur="handleInputConfirm(scope.row)"> </el-input>
                <!-- 添加tag按钮 -->
                <el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ New Tag</el-button>
              </template>
            </el-table-column>
            <el-table-column label="#" type="index"></el-table-column>
            <el-table-column prop="attr_name" label="参数名称"> </el-table-column>
            <el-table-column label="操作">
              <template v-slot="scope">
                <el-button @click="editBtnDialog(scope.row)" icon="el-icon-edit" type="primary" size="mini">编辑</el-button>
                <el-button @click="removeParams(scope.row.attr_id)" icon="el-icon-delete" type="danger" size="mini">删除</el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>
    </el-card>

    <!-- 添加参数对话框 -->
    <el-dialog @close="addFormClose" :title="'添加' + dialogInfo" :visible.sync="addDialogVisible" width="50%">
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="100px">
        <el-form-item :label="dialogInfo" prop="attr_name">
          <el-input v-model="addForm.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="addParamsDialog">确 定</el-button>
      </span>
    </el-dialog>

    <!-- 修改参数对话框 -->
    <el-dialog @close="editFormClose" :title="'添加' + dialogInfo" :visible.sync="editDialogVisible" width="50%">
      <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="100px">
        <el-form-item :label="dialogInfo" prop="attr_name">
          <el-input v-model="editForm.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="editParamsDialog">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  name: 'Params',
  data() {
    return {
      // 商品分类列表
      cateListInfo: [],
      // 级联选择器属性
      cateCascaderProps: {
        expandTrigger: 'hover',
        value: 'cat_id',
        label: 'cat_name',
        children: 'children'
      },
      // 级联选择器选中的数据
      selectedCateKeys: [],
      // teb标签页分类
      cateActiveName: 'many',
      // teb动态参数数据
      manyTableData: [],
      // tab静态属性数据
      onlyTableData: [],
      // 添加参数表单数据
      addForm: {
        attr_name: ''
      },
      // 添加参数对话框显示与隐藏
      addDialogVisible: false,
      // 添加参数form表单验证规则
      addFormRules: {
        attr_name: [{ required: true, message: '请输入参数名称', trigger: 'blur' }]
      },
      // 修改参数form表单数据
      editForm: {
        // attr_name: ''
      },
      // 控制修改参数对话框显示与隐藏
      editDialogVisible: false,
      editFormRules: {
        attr_name: [{ required: true, message: '请输入参数名称', trigger: 'blur' }]
      },
      // 控制tag添加显示与隐藏
      inputVisible: false,
      // 添加tag input数据
      inputValue: ''
    }
  },
  created() {
    this.getCateList()
  },
  methods: {
    // 获取商品分类列表
    async getCateList() {
      const { data: res } = await this.$http.get('/categories/')
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg)
      }
      this.cateListInfo = res.data
      console.log(this.cateListInfo)
    },
    // 级联选择器监听事件
    async cateHandleChange() {
      this.getParamsList()
      // if (this.selectedCateKeys.length !== 3) {
      //   this.selectedCateKeys = []
      // }
      // console.log(this.selectedCateKeys)
      // const { data: res } = await this.$http.get(`/categories/${this.cateId}/attributes`, { params: { sel: this.cateActiveName } })
      // if (res.meta.status !== 200) {
      //   return this.$message.error(res.meta.msg)
      // }
      // console.log(res.data)
    },
    // teb标签页监听事件
    cateHandleClick() {
      console.log(this.cateActiveName)
      this.getParamsList()
    },
    // 获取分类属性
    async getParamsList() {
      // 判断是否选择了三级分类
      if (this.selectedCateKeys.length !== 3) {
        this.selectedCateKeys = []
        // 没有选中时，清空动态属性和静态属性数据
        this.manyTableData = []
        this.onlyTableData = []
        return
      }
      // console.log(this.selectedCateKeys)
      const { data: res } = await this.$http.get(`/categories/${this.cateId}/attributes`, { params: { sel: this.cateActiveName } })
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg)
      }
      res.data.forEach(item => {
        // const attrVals = item.attr_vals.split(',')
        // if (attrVals[0] !== '') {
        //   item.attr_vals = attrVals
        // }

        item.attr_vals = item.attr_vals ? item.attr_vals.split(',') : []
        // 添加文本框的显示和隐藏
        item.inputVisible = false
        // 文本框的数据
        item.inputValue = ''
      })

      console.log(res.data)

      if (this.cateActiveName === 'many') {
        this.manyTableData = res.data
      } else {
        this.onlyTableData = res.data
      }
    },
    // 添加参数对话框回调，清空表单
    addFormClose() {
      this.$refs.addFormRef.resetFields()
    },
    //  添加参数axios
    addParamsDialog() {
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.$http.post(`/categories/${this.cateId}/attributes`, {
          attr_name: this.addForm.attr_name,
          attr_sel: this.cateActiveName
        })
        if (res.meta.status !== 201) {
          return this.$message.error(res.meta.msg)
        }

        this.$message.success('添加参数成功！')
        this.getParamsList()
        this.addDialogVisible = false
      })
    },
    // 发起axios修改编辑参数
    async editParamsDialog() {
      const { data: res } = await this.$http.put(`/categories/${this.cateId}/attributes/${this.editForm.attr_id}`, { attr_name: this.editForm.attr_name, attr_sel: this.cateActiveName })
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg)
      }
      this.$message.success('修改分类成功！')
      this.getParamsList()
      this.editDialogVisible = false
    },
    // 修改参数对话框回调事件
    editFormClose() {
      this.$refs.editFormRef.resetFields()
    },
    // 按钮点击事件，点击或获取input框数据进行填充
    editBtnDialog(editForm) {
      this.editForm = editForm
      console.log(this.editForm)
      this.editDialogVisible = true
    },
    // 根据id删除参数属性
    async removeParams(id) {
      // box确认弹出框
      const confirmResult = await this.$confirm('此操作将永久删除该参数, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).catch(err => err)
      if (confirmResult !== 'confirm') {
        return this.$message.info('取消删除成功！')
      }

      const { data: res } = await this.$http.delete(`/categories/${this.cateId}/attributes/${id}`)
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg)
      }
      this.$message.success('删除参数成功！')
      this.getParamsList()
      this.editDialogVisible = false
    },
    // 添加tag事件
    async handleInputConfirm(row) {
      if (row.inputValue.trim().length === 0) {
        row.inputValue = ''
        row.inputVisible = false
        return
      }
      // 没有retrn表示是正常添加
      // 正常输入的input值追加到数组
      row.attr_vals.push(row.inputValue.trim())
      row.inputValue = ''
      row.inputVisible = false

      // 调用修改参数方法，更新修改标签
      this.initTagList(row)
    },
    // 控制文本框显示与隐藏
    showInput(scope) {
      scope.inputVisible = true
      // 获取文本框焦点
      // $nextTick方法当页面重新渲染完后，才会执行回调
      this.$nextTick(_ => {
        this.$refs.saveTagInput.$refs.input.focus()
      })
    },
    // 提交修改编辑参数，更新数据
    async initTagList(row) {
      const { data: res } = await this.$http.put(`/categories/${this.cateId}/attributes/${row.attr_id}`, {
        attr_name: row.attr_name,
        attr_sel: row.attr_sel,
        attr_vals: row.attr_vals.join(',')
      })
      console.log(row.attr_vals.join(','))
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg)
      }
      this.$message.success('添加Tag标签成功！')
    },
    // 删除tag标签
    // i 为tag循环体中index
    removeParamsTag(i, row) {
      // 删除点击删除tag索引标签
      row.attr_vals.splice(i, 1)
      // 调用编辑提交修改参数方法
      this.initTagList(row)
    }
  },

  computed: {
    // 判断级联选择器是否选择三级菜单
    // 选择返回false 不禁用button
    // 否则返回true 禁用button
    isBtnDisabled() {
      if (this.selectedCateKeys.length !== 3) {
        return true
      }
      return false
    },
    // 获取级联选择器3级的分类id
    cateId() {
      if (this.selectedCateKeys.length === 3) {
        return this.selectedCateKeys[2]
      }
      return null
    },
    // 计算tab页输出input框显示名称
    dialogInfo() {
      if (this.cateActiveName === 'many') {
        return '动态参数'
      } else {
        return '静态属性'
      }
    }
  }
}
</script>

<style lang="less" scoped>
.goods-par {
  margin: 20px;
}
.el-cascader {
  width: 300px;
}
.tagParams {
  margin: 10px;
}
.input-new-tag {
  width: 120px;
}
</style>
