<template>
  <div>
    <!-- 面包屑导航 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>商品分类</el-breadcrumb-item>
    </el-breadcrumb>

    <!-- 卡片区域 -->
    <el-card>
      <!-- 添加分类按钮 -->
      <el-row>
        <el-col>
          <el-button @click="addCateSetUp" type="primary">添加分类</el-button>
        </el-col>
      </el-row>

      <!-- tree-table -->
      <!-- 表格 -->
      <tree-table class="treeTable" :show-row-hover="false" index-text="#" border show-index :expand-type="false" :selection-type="false" :data="catelist" :columns="columns">
        <!-- 是否有效 -->
        <template v-slot:likes="scope">
          <i style="color: lightgreen;" class="el-icon-success" v-if="scope.row.cat_deleted === false"></i>
          <i style="color:red;" class="el-icon-error" v-else></i>
        </template>
        <!-- 排序 -->
        <template v-slot:sotr="scope">
          <el-tag size="mini" v-if="scope.row.cat_level === 0">一级</el-tag>
          <el-tag type="success" size="mini" v-else-if="scope.row.cat_level === 1">二级</el-tag>
          <el-tag type="danger" size="mini" v-else>三级</el-tag>
        </template>
        <!-- 操作 -->
        <template v-slot:opt="socpe">
          <el-button @click="editCate(socpe.row.cat_id, socpe.row.cat_name)" size="mini" type="primary" icon="el-icon-edit">编辑</el-button>
          <el-button @click="deleteCate(socpe.row.cat_id)" size="mini" type="danger" icon="el-icon-delete">删除</el-button>
        </template>
      </tree-table>

      <!-- 页码 -->
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="querGoodsPage.pagenum" :page-sizes="[3, 5, 10, 15]" :page-size="querGoodsPage.pagesize" layout="total, sizes, prev, pager, next, jumper" :total="total"> </el-pagination>
    </el-card>

    <!-- 添加分类对话框 -->
    <el-dialog @close="addDialogClose" title="添加分类" :visible.sync="addCateDialogVisible" width="50%">
      <!-- 表单 -->
      <el-form :model="addCateForm" :rules="addCateFormRules" ref="addCateFormRef" label-width="100px">
        <el-form-item label="分类名称" prop="cat_name">
          <el-input v-model="addCateForm.cat_name"></el-input>
        </el-form-item>
        <el-form-item label="父级分类">
          <el-cascader clearable v-model="selectedKeys" :options="addCateParentData" :props="addCateCascaderProps" @change="handleChange"></el-cascader>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addCateDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="addCate">确 定</el-button>
      </span>
    </el-dialog>

    <!-- 编辑分类对话框 -->
    <el-dialog title="编辑" :visible.sync="editDialogVisible" width="50%">
      <el-form :rules="editFormRules" ref="editFormRef" :model="editForm" label-width="80px">
        <el-form-item label="分类名称" prop="cat_name">
          <el-input v-model="editForm.cat_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="editCateDialogVisible">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  name: 'Cate',
  data() {
    return {
      querGoodsPage: {
        // 当前页码
        pagenum: 1,
        // 每页显示多少条
        pagesize: 5
      },
      // 商品分类数据
      catelist: [],
      // 总数据条数
      total: 0,
      columns: [
        {
          label: '分类名称',
          prop: 'cat_name'
        },
        {
          label: '是否有效',
          // 定义为作用域插槽
          type: 'template',
          // 定义插槽名称
          template: 'likes'
        },
        {
          label: '排序',
          // 定义为作用域插槽
          type: 'template',
          // 定义插槽名称
          template: 'sotr'
        },
        {
          label: '操作',
          // 定义为作用域插槽
          type: 'template',
          // 定义插槽名称
          template: 'opt'
        }
      ],
      // 添加分类显示与隐藏
      addCateDialogVisible: false,
      // 添加分类input数据
      addCateForm: {
        cat_name: '',
        // 父级分类等级默认一级
        cat_level: 0,
        // 父级分类di
        cat_pid: 0
      },
      // 添加分类验证
      addCateFormRules: {
        cat_name: [{ required: true, message: '请输入分类名称', trigger: 'blur' }]
      },
      // 父级分类data数据
      addCateParentData: [],
      addCateCascaderProps: {
        expandTrigger: 'hover',
        value: 'cat_id',
        label: 'cat_name',
        children: 'children',
        checkStrictly: true
      },
      // 联级选择器选中数据
      selectedKeys: [],
      // 编辑form表单数据
      editForm: {
        cat_name: ''
      },
      // 编辑表单验证
      editFormRules: {
        cat_name: [{ required: true, message: '请输入分类名称', trigger: 'blur' }]
      },
      // 编辑对话框显示与隐藏
      editDialogVisible: false,
      // 编辑分类查询的分类名称
      cateNameInfo: '',
      // 编辑分类id
      cateNameId: ''
    }
  },
  created() {
    this.getGoodsCateList()
  },
  methods: {
    async getGoodsCateList() {
      const { data: res } = await this.$http.get('/categories', { params: this.querGoodsPage })
      if (res.meta.status !== 200) {
        return this.$message.error('获取商品列表失败')
      }

      this.catelist = res.data.result
      this.total = res.data.total
    },
    // pageSize 改变时会触发
    handleSizeChange(pagesize) {
      this.querGoodsPage.pagesize = pagesize
      this.getGoodsCateList()
    },
    // currentPage 改变时会触发
    handleCurrentChange(currentpage) {
      this.querGoodsPage.pagenum = currentpage
      this.getGoodsCateList()
    },
    // 添加分类
    addCateSetUp() {
      // 调用父级列表方法获取数据
      this.addCateParentList()
      this.addCateDialogVisible = true
    },
    // 获取父级分类列表
    async addCateParentList() {
      const { data: res } = await this.$http.get('/categories', { params: { type: 2 } })
      if (res.meta.status !== 200) {
        return this.$message.error('获取父级分类列表失败！')
      }

      this.addCateParentData = res.data
    },
    // 获取联级选择器选中状态
    handleChange() {
      // console.log(this.selectedKeys)
      if (this.selectedKeys.length > 0) {
        // 父级分类id

        this.addCateForm.cat_pid = this.selectedKeys[this.selectedKeys.length - 1]
        // 当前添加的分类等级
        this.addCateForm.cat_level = this.selectedKeys.length
      } else {
        this.addCateForm.cat_pid = 0
        this.addCateForm.cat_level = 0
      }
    },
    // 添加分类
    addCate() {
      this.$refs.addCateFormRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.$http.post('/categories', this.addCateForm)

        if (res.meta.status !== 201) {
          return this.$message.error('添加分类失败！')
        }
        this.$message.success('添加分类成功！')

        this.getGoodsCateList()
        this.addCateDialogVisible = false
      })
    },
    // 对话框关闭后回调
    addDialogClose() {
      // 清空表单
      this.$refs.addCateFormRef.resetFields()
      // 清空联级选择器
      this.selectedKeys = []
      this.addCateForm.cat_level = 0
      this.addCateForm.cat_pid = 0
    },
    // 编辑分类
    async editCate(id) {
      this.cateNameId = id

      const { data: res } = await this.$http.get(`/categories/${id}`)
      if (res.meta.status !== 200) {
        return this.$message.error('获取分类名称失败！')
      }
      this.editForm.cat_name = res.data.cat_name
      this.editDialogVisible = true
    },
    // 编辑分类axios请求修改
    editCateDialogVisible() {
      this.$refs.editFormRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.$http.put(/categories/ + this.cateNameId, { cat_name: this.editForm.cat_name })
        if (res.meta.status !== 200) {
          return this.$message('编辑分类名称失败！')
        }
        this.$message.success('编辑分类名称成功！')
        this.getGoodsCateList()

        this.editDialogVisible = false
      })
    },
    // 根据id删除商品分类
    async deleteCate(id) {
      const confirm = await this.$confirm('此操作将永久删除该商品分类, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).catch(err => err)

      if (confirm !== 'confirm') {
        return this.$message.info('取消删除成功')
      }
      const { data: res } = await this.$http.delete(`/categories/${id}`)
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg)
      }
      this.$message.success(res.meta.msg)
      this.getGoodsCateList()
    }
  }
}
</script>

<style lang="less" scoped>
.treeTable {
  margin-top: 15px;
}
</style>
